@import url(https://fonts.googleapis.com/css?family=Montserrat:100,300,400,600,800|Slabo+27px);
img.app-design {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1; }

header.header {
  width: 1356px;
  margin: auto; }
  header.header .header-top-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 112px; }
    header.header .header-top-bar .navigation-menu ul {
      display: flex; }
      header.header .header-top-bar .navigation-menu ul li:not(:first-child) {
        margin-left: 38px; }
      header.header .header-top-bar .navigation-menu ul li a {
        font-size: 18px;
        color: white;
        text-transform: uppercase;
        text-decoration: none;
        text-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
        letter-spacing: 0.1px; }
  header.header .position-titles {
    margin-top: 210px;
    text-align: center; }
    header.header .position-titles p {
      font-size: 57px;
      font-weight: 500;
      line-height: 70px; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  font-family: "Montserrat", sans-serif;
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

